#Atm {
    display: flex;
    flex-direction: column;

    height: 60%;
    width: 60%;
    background: url(../../assets/images/background.png);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 1rem;

    label.Mui-focused {
        color: #30C776;
    }
    .MuiInput-underline:after {
        border-bottom-color: #30C776;
    }
    .Mui-focused fieldset {
        border-color: #30C776;
    }

    > .header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        > img {
            height: 2.5rem;
        }
    }

    > .saldoContainer {
        width: 60%;
        margin-top: 8rem;
        align-self: center;
        text-align: center;
    }

}